<template>
    <div class="addAccountNumber">
        <div class="bx" >
            <h2 v-if="this.$route.params.key == 'add'">新增账号</h2>
            <h2 v-if="this.$route.params.key == 'modify'">修改账号</h2>
            <h2 v-if="this.$route.params.key == 'look'">查看信息</h2>
            <div class="ul-dv">
                <ul>
                <li>
                    <span>用户名</span>
                    <input type="text" v-if="this.$route.params.key == 'add'" v-model="username">
                    <input type="text" v-if="this.$route.params.key == 'modify'" v-model="username" disabled>
                    <input type="text" v-if="this.$route.params.key == 'look'" v-model="username" disabled>
                </li>
                <li>
                    <span>密码</span>
                    <input type="text" v-model="password" v-if="this.$route.params.key == 'add'">
                    <input type="text" v-model="password" v-if="this.$route.params.key == 'modify'" disabled>
                    <input type="text" v-model="password" v-if="this.$route.params.key == 'look'" disabled>
                </li>
                <li>
                    <span>姓名</span>
                    <input type="text" v-model="name">
                </li>
                <li>
                    <span>所属公司</span>
                    <input type="text" v-model="companyName">
                </li>
                <li>
                    <span>联系电话</span>
                    <input type="text" v-model="mobile">
                </li>
                <li>
                    <span>用户类型</span>
                    <el-select v-model="value1" placeholder="请选择">
                        <el-option
                            v-for="item in accountType"
                            :key="item.value"
                            :label="item.label"
                            :value="item.label">
                        </el-option>
                    </el-select>
                </li>
                 <li>
                    <span>账号状态</span>
                    <el-select v-model="value2" placeholder="请选择">
                        <el-option
                            v-for="item in accountStatus"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </li>
                 <li>
                    <span>用户角色</span>
                    <el-select v-model="value3" placeholder="请选择" @change="selectValue">
                        <el-option
                            v-for="item in userRoles"
                            :key="item.id"
                            :label="item.roleName"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </li>
                 <li v-if="getRoleName !== '普通用户'">
                    <span>角色有效期</span>
                    <input v-if="this.$route.params.key == 'look'" type="text" v-model="roleTime">
                    <input v-else-if="this.$route.params.key != 'look'" type="text" v-model="days">
                    <i v-if="this.$route.params.key != 'look'">天</i>
                </li>

                <li>
                    <span>备注信息</span>
                    <input type="text" v-model="remake">
                </li>
            </ul>
            </div>
            <div class="btns">
                <el-button type="primary" @click="addUser()" v-if="this.$route.params.key == 'add'">确定</el-button>
                <el-button type="primary" @click="modifyUser()" v-if="this.$route.params.key == 'modify'">确定</el-button> 
                <el-button type="info" plain @click="goBack()">取消</el-button>
            </div>
        </div>

    </div>
</template>
<script>
import Axios from 'axios'
import { javaServiceUrl } from './../../util/constant.js'

export default {
    data(){
        return{
            //账户类型
            accountType:[{
                value:'1',
                label:"国内量化基金"
            },{
                value:'2',
                label:"海外量化基金"
            },{
                value:'3',
                label:"国内基本面基金"
            },{
                value:'4',
                label:"海外基本面基金"
            },{
                value:"5",
                label:"其他"
            }],
            accountStatus: [{
                value: '0',
                label: '使用中'
            }, {
                value: '-1',
                label: '已停用'
            },{
                value:'1',
                label:"禁止媒体下载文件"
            }],
            userRoles:[],//用户角色
            value1: '', //用户类型
            value2: '',//账户状态
            value3:'',//用户角色
            modifyStatus:"",//修改账户状态
            username:'',//用户名
            password:'',//密码
            name:"",//姓名
            companyName:'',//所属公司
            mobile:'',//联系电话
            days:'0',//角色有效期
            modify:{},//修改账号内容
            modifyId:"",//修改需要传的id
            remake:'',//备注信息
            roleName:"",//用户角色
            getRoleName:"" ,//获取用户角色 控制角色有效期的显示和隐藏
            authorization:"",//请求头
            roleTime:"",//有效期
            token:"",
        }
    },
     created(){
        var storage = window.localStorage;
        this.authorization = storage.getItem("userId");
        this.token = storage.getItem("token");
    },
    mounted(){
        if(this.$route.params.key != 'add'){
            this.modify = this.$route.params.row;
            this.password = "******";
            this.name = this.modify.name;
            this.companyName = this.modify.companyName;
            this.mobile = this.modify.mobile;
            this.value1 = this.modify.type;
            //用户状态
            this.value2 = this.modify.status;
            if(this.modify.status == '使用中' ){
                this.value2 = "0";
            }else if(this.modify.status == '已停用'){
                this.value2 ="-1";
            }else if(this.modify.status == '禁止媒体下载文件'){
                this.value2 = "1";
            }
            this.days = this.modify.days;
            this.value3 = this.modify.roleId;
            this.modifyId = this.modify.id;
            this.username = this.modify.username;
            this.remake = this.modify.remake
            this.roleName = this.modify.roleName
            this.roleTime = this.modify.roleTime;//有效期
        }
        this.getUserRoles();
    },
   
    methods:{
        //获取用户角色
        selectValue(a) {
            let obj = {};
            obj = this.userRoles.find((item)=>{
                return item.id === a;
            })
            this.getRoleName = obj.roleName;
        },
        // 修改
        modifyUser(){
            Axios({
               url: javaServiceUrl + '/userPermission/insertOrUpdateWebUser',
               method: 'post',
               headers: {"Authorization":"user-" + this.authorization + "-" +this.token},     
               data:{
                    name:this.name,
                    status:this.value2,
                    roleId:this.value3,
                    type:this.value1,
                    mobile:this.mobile,
                    companyName:this.companyName,
                    days:this.days,
                    remake: this.remake,
                    id:this.modifyId,
                }
            })
            .then(res=>{
                if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                    this.$router.push({name: 'userMnagement'});
                    this.$message({
                        type:'success',
                        message:"更新"+res.data.code+'条！',
                    })
                }
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //添加
        addUser(){
            if(this.password.length < 6 || this.password.length >20){
                this.$message({
                    message: '密码长度为6-20位!',
                    type: 'warning'
                });
                return false;
            };
            Axios({
               url:javaServiceUrl + '/userPermission/insertOrUpdateWebUser',
               method: 'post',
               headers: {"Authorization":"user-" + this.authorization + "-" +this.token},  
               data:{
                    username:this.username,
                    password:this.password,
                    name:this.name,
                    status:0,
                    roleId:this.value3,
                    type:this.value1,
                    mobile:this.mobile,
                    companyName:this.companyName,
                    days:this.days,
                    remake:this.remake,
                }
            })
            .then(res=>{
                if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                    this.$router.push({name: 'userMnagement'});
                    this.$message({
                        type:'success',
                        message:"更新"+res.data.code+'条！',
                    })
                }
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //取消
        goBack(){
            this.$router.go(-1);
        },
        //用户角色
        getUserRoles(){
            Axios({
               url:javaServiceUrl + '/userPermission/findRoles',
               method: 'get',
               headers: {"Authorization":"user-" + this.authorization + "-" +this.token},  
               params:{
                    roleName:"",
                    pageSize:1000,
                    pageNum:0
                }
            })
            .then(res=>{
                if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                    this.userRolesTemp = res.data.list;
                    for( let i = 0;i < this.userRolesTemp.length; i++ ){
                        if(this.userRolesTemp[i].state != -2){
                            this.userRoles.push(this.userRolesTemp[i]);
                        }
                    }
                    this.roleName = this.userRolesTemp[i].roleName;
                }
            })
            .catch(err=>{
                console.log(err)
            })
        }

        
    }
}
</script>

<style lang="scss" scoped>
.addAccountNumber{
    width: 100%;
    .bx{
        padding-top: 20px;
        // width: 500px !important;

        h2{
            font-size: 20px;
            margin: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .ul-dv{
            display: flex;
            justify-content: center;
            align-items: center;
       
        }
        width: 100%;
        height: 100%;
        background: #fff;
        ul{
            li{
                margin: 20px 30px;
                input{
                    width: 300px;
                    height: 40px;
                    border-radius: 4px;
                    border: 1px solid #DCDFE6;
                } 
                span{
                    display: inline-block;
                    width: 80px;
                    text-align: right;
                    margin-right: 10px;
                }
            }
        }
        .btns{
            margin: 30px 350px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}
</style>



